body[fds] snack-bar-container {
  background: #fff;
  padding: 0;
  box-shadow: 0 0 3px 0 #1391c1; }

fds-snackbar-title mat-icon.mat-icon.mat-primary.material-icons {
  font-size: 10px;
  height: 10px;
  width: 10px;
  line-height: 10px;
  color: #999; }

.fds-coaster-message {
  font-size: 12px; }

.fds-snackbar-title {
  font-size: 14px; }

.fds-snackbar-title i {
  font-size: 24px; }

.fds-coaster-icon {
  position: absolute;
  top: 19px;
  left: 15px; }

.coaster-close-icon {
  height: 10px !important;
  width: 10px !important;
  line-height: 10px !important;
  right: 10px;
  position: absolute !important;
  top: 10px; }

.fds-snackbar-wrapper {
  border-radius: 2px;
  border-width: 1px;
  border-style: solid; }
